/*==================== GOOGLE FONTS ====================*/
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap');

/*==================== VARIABLES CSS ====================*/
:root {
    --header-height: 4.5rem;
    --sidemenu-width: 3rem;
    --sidemenu-open: 18rem;

    /*========== Colors ==========*/
    /* Change favorite color */
    --hue-color: 176; /*Purple 250 - Green 176 - Blue 230 - Pink 340*/
    --white-color: rgb(255, 255, 255);

    /* HSL color mode */
    --first-color: hsl(var(--hue-color), 57%, 53%);
    --first-color-second: hsl(var(--hue-color), 30%, 8%);
    --first-color-alt: hsl(var(--hue-color), 52%, 48%);
    --first-color-lighter: hsl(var(--hue-color), 92%, 85%);
    --title-color: hsl(var(--hue-color), 8%, 95%);
    --text-color: hsl(var(--hue-color), 8%, 75%);
    --text-color-lighter: hsl(var(--hue-color), 8%, 85%);
    --input-color: hsl(var(--hue-color), 1%, 30%);
    --body-color: hsl(var(--hue-color), 8%, 8%);
    --container-color: hsl(var(--hue-color), 15%, 15%);
    --card-color: hsl(var(--hue-color), 29%, 16%);
    --scroll-bar-color: hsl(var(--hue-color), 12%, 8%);
    --scroll-thumb-color: hsl(var(--hue-color), 12%, 36%);

    /*========== Font and typography ==========*/
    --body-font: 'Poppins', sans-serif;

    /* .5rem = 8px, 1rem = 16px, 1.5rem = 24px ... */
    --big-font-size: 2rem;
    --h1-font-size: 1.5rem;
    --h2-font-size: 1.25rem;
    --h3-font-size: 1.125rem;
    --normal-font-size: 0.938rem;
    --small-font-size: 0.813rem;
    --smaller-font-size: 0.75rem;

    /*========== Font weight ==========*/
    --font-medium: 500;
    --font-semi-bold: 600;

    /*========== Margenes Bottom ==========*/
    /* .25rem = 4px, .5rem = 8px, .75rem = 12px ... */
    --mb-0-25: 0.25rem;
    --mb-0-5: 0.5rem;
    --mb-0-75: 0.75rem;
    --mb-1: 1rem;
    --mb-1-5: 1.5rem;
    --mb-2: 2rem;
    --mb-2-5: 2.5rem;
    --mb-3: 3rem;

    /*========== z index ==========*/
    --z-tooltip: 10;
    --z-fixed: 100;
    --z-modal: 1000;
}

/* Font size for large devices */
@media screen and (min-width: 968px) {
    :root {
        --big-font-size: 3rem;
        --h1-font-size: 2.25rem;
        --h2-font-size: 1.5rem;
        --h3-font-size: 1.25rem;
        --normal-font-size: 1rem;
        --small-font-size: 0.875rem;
        --smaller-font-size: 0.813rem;
        --sidemenu-width: 18rem;
    }
}

/*==================== BASE ====================*/
*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: var(--body-font);
  font-size: var(--normal-font-size);
  background-color: var(--body-color);
  color: var(--text-color);
}

h1,h2,h3,h4 {
  color: var(--title-color);
  font-weight: var(--font-semi-bold);
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

hr {
  margin: 2rem 0;
  border-color: rgba(255, 255, 255, 0.075);
}

/*==================== REUSABLE CSS CLASSES ====================*/
.section {
  padding: 2rem 0 4rem;
}

.padding {
  padding: var(--mb-1);
}

.section_title, .section_subtitle  {
  text-align: center;
}

.section_subtitle {
  margin-bottom: var(--mb-2-5);
}

/*==================== LAYOUT ====================*/
.container {
  max-width: 768px;
  margin-left: var(--mb-1-5);
  margin-right: var(--mb-1-5);
}

.grid {
  display: grid;
  gap: 1.5rem;
}

.flex {
  display: flex;
}

.justify-content-between {
  justify-content: space-between;
}

.center {
  justify-content: center;
}

/*==================== BUTTONS ====================*/
.button {
  display: inline-block;
  background-color: var(--first-color-alt);
  color: var(--white-color);
  padding: 0.8rem;
  border-radius: .2rem;
  font-weight: var(--font-medium);
  cursor: pointer;
}

.button:hover {
  background-color: var(--first-color);
}

.button_icon {
  font-size: 1.25rem;
  margin-left: var(--mb-0-5);
  transform: 0.3s;
}

.button--flex {
  display: inline-flex;
  align-items: center;
}

.button--small {
  padding: 0.6rem 1rem;
}

.button--smaller {
  padding: 0.5rem 1rem;
  font-size: var(--smaller-font-size);
}

.button--link {
  padding: 0;
  background-color: transparent;
  color: var(--first-color);
}

.button--link:hover {
  background-color: transparent;
  color: var(--first-color-alt);
}

.button--white {
  background-color: var(--white-color);
  color: var(--input-color);
}

.button--white:hover {
  background-color: rgb(245, 245, 245);
}

.button-danger {
  background-color: rgb(218, 92, 92);
}

.button-primary {
  background-color: rgb(92, 140, 218);
}

.button--center {
  width: 30%;
  margin: auto;
}

/*==================== INPUTS ====================*/

input, textarea, select {
  width: 100%;
  font-family: var(--body-font);
  font-size: var(--small-font-size);
  border: none;
  outline: none;
  padding: 0.5rem;
}

/* input::placeholder, textarea::placeholder {
  color: var(--text-color);
} */

.input-dark {
  background-color: var(--input-color);
  color: var(--text-color-lighter);
}

.input-dark::placeholder {
  color: var(--text-color);
}

.input-disabled {
  background-color: rgb(46, 46, 46);
  color: var(--text-color);
}

/*==================== ALERTS ====================*/

.alert {
  position: relative;
  padding: var(--mb-0-5);
  font-size: var(--smaller-font-size);
  border: 1px solid transparent;
  border-radius: .25rem;
}

.alert-danger {
  color: #721c24;
  background-color: #f8d7da;
  border-color: #f5c6cb;
}

.alert-success {
  color: #155724;
  background-color: #d4edda;
  border-color: #c3e6cb;
}

.alert-info {
  color: #0c5460;
  background-color: #d1ecf1;
  border-color: #bee5eb;
}

.alert-warning {
  color: #856404;
  background-color: #fff3cd;
  border-color: #ffeeba;
}

.alert-primary {
  color: #004085;
  background-color: #cce5ff;
  border-color: #b8daff;
}

/*alert swal*/

.swal2-popup,
.swal2-success-circular-line-left,
.swal2-success-circular-line-right,
.swal2-success-fix
{
  background-color: var(--body-color) !important;
}

.swal2-title {
  color: var(--text-color-lighter) !important;
}

.swal2-html-container {
  color: var(--text-color) !important;
}
/*==================== SWIPER ====================*/

.swiper-container {
  width: 100%;
  height: 100%;
}

/*========== SCROLL BAR ==========*/
::-webkit-scrollbar {
  width: 0.6rem;
  background-color: var(--scroll-bar-color);
  /* border-radius: 0.5rem; */
}

::-webkit-scrollbar-thumb {
  background-color: var(--scroll-thumb-color);
  border-radius: 0.5rem;
}

::-webkit-scrollbar-thumb:hover {
  background-color: var(--text-color);
}

/*==================== MEDIA QUERIES ====================*/
/* For small devices */
@media screen and (max-width: 350px) {
  .container {
    margin-left: var(--mb-1);
    margin-right: var(--mb-1);
  }
}

@media screen and (min-width: 768px) {
  .padding {
    padding: var(--mb-2-5);
  }
  .container {
    margin-left: auto;
    margin-right: auto;
  }
  body {
    margin: 0;
  }
  .section {
    padding: 6rem 2rem 2rem;
  }
  .section__subtitle {
    margin-bottom: 3rem;
  }
}